@import url(partials/reset.css);

@import url(partials/fonts.css);

@import url(libs/swiper.css);

@import url(libs/jquery.fancybox.css);

/* my style */

a {
  text-decoration: none;
}

html {
  font-family: sans-serif;
  line-height: 1.2;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

body {
  margin: 0;
  font-size: 1rem;
  color: #000000;
  font-weight: 400;
  font-family: "PT Serif Pro";
  text-align: left;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 1170px;
}

img {
  max-width: 100%;
  height: auto;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 179px;
  height: 51px;
  border-radius: 63% 8% 44% 10%/86% 23% 88% 24%;
  background: #da1f8d;
  box-shadow: 0 26px 46px rgba(0, 0, 0, 0.24), 0 3px 0 #952165;
  font-family: "Roboto";
  color: #ffffff;
  font-size: 12px;
  font-weight: 700;
  border: none;
  cursor: pointer;
  user-select: none;
  transition-duration: .2s;
}

.btn_color_purple {
  box-shadow: 0 29px 27px rgba(5, 93, 143, 0.22), 0 2px 0 rgba(104, 35, 154, 0.55), inset 0 -1px 0 rgba(0, 0, 0, 0.53), inset 0 1px 0 rgba(255, 255, 255, 0.75);
  background-image: linear-gradient(to top, #a650e1 0%, #b362e3 100%);
}

.btn--blue {
  background-image: linear-gradient(to top, #0086d8 0%, #30bfff 100%);
  box-shadow: 0 29px 27px rgba(5, 93, 143, 0.22), 0 2px 0 rgba(0, 48, 86, 0.55), inset 0 -1px 0 rgba(0, 0, 0, 0.53), inset 0 1px 0 rgba(255, 255, 255, 0.75);
}

.btn:hover,
.btn:focus {
  opacity: .7;
  outline: none;
}

.block-title {
  color: #000000;
  font-size: 36px;
  font-weight: 700;
  font-family: "DejaVu Serif";
  font-style: italic;
  text-align: center;
}

.block-subtitle {
  color: #00baff;
  font-family: "PT Serif Pro";
  font-size: 37px;
  font-weight: 700;
  text-align: center;
}

.callback-form {
  margin: 0 auto;
}

.callback-form__input-row {
  display: flex;
  justify-content: center;
}

.callback-form__input {
  width: 191px;
  padding-bottom: 5px;
  background: transparent;
  border: none;
  color: #6d90a1;
  font-family: "Circe";
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  box-shadow: 0 2px 0 rgba(53, 191, 253, 0.33);
  border-bottom: 2px solid #00a8ff;
  transition: all 0.2s ease;
  margin: 0 12px;
}

.callback-form__input::placeholder {
  color: #6d90a1;
  font-family: "Circe";
  font-size: 14px;
  font-weight: 400;
  text-align: center;
}

.callback-form__input:hover,
.callback-form__input:focus {
  outline: none;
  color: #000000;
  border-color: #a5e0ff;
}

.callback-form__input:hover::placeholder,
.callback-form__input:focus::placeholder {
  color: #000000;
}

.callback-form__input:valid {
  box-shadow: 0 2px 0 rgba(138, 169, 184, 0.33);
  border-color: #6c90a1;
}

.callback-form__button {
  margin: 30px auto 0;
  width: 198px;
}

.callback-form__info {
  margin: 25px auto 0;
  width: 550px;
  max-width: 100%;
  color: #000000;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 300;
  text-align: center;
}

.fotoslider {
  position: relative;
}

.fotoslider__container {
  width: 1050px;
  max-width: 100%;
  margin: 0 auto;
}

.fotoslider__slide {
  width: 749px;
  height: 412px;
}

.fotoslider__slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fotoslider__button {
  display: block;
  width: 33px;
  height: 33px;
  position: absolute;
  top: 215px;
  cursor: pointer;
  background: url("../img/next.svg") center center no-repeat;
  background-size: contain;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.fotoslider__button::before,
.fotoslider__button::after {
  display: none;
}

.fotoslider__button:focus {
  outline: none;
}

.fotoslider__button:hover {
  opacity: 0.5;
}

.fotoslider__button_type_prev {
  transform: scale(-1, 1);
  left: 17px;
}

.fotoslider__button_type_next {
  right: 17px;
}

.fotoslider__pagination {
  width: 278px;
  height: 4px;
  margin: 29px auto 0;
  position: static;
  display: flex;
}

.fotoslider__pagination .swiper-pagination-bullet {
  width: auto;
  height: 100%;
  border-radius: 0;
  flex-grow: 1;
}

.fotoslider__pagination .swiper-pagination-bullet-active {
  background: #009cff;
}

.fotoslider__pagination .swiper-pagination-bullet + .swiper-pagination-bullet {
  margin-left: 2px;
}

.fotoslider__pagination .swiper-pagination-bullet:focus {
  outline: none;
}

.hidden-content {
  display: none;
}

.social-fixed-block {
  display: flex;
