$font1: 'PT Serif Pro';
$font2: 'DejaVu Serif';
$font3: 'Futura PT';
$font4: 'Montserrat';
$font5: 'Circe';
$font6: 'Roboto';

a {
	text-decoration: none;
}

html {
	font-family: sans-serif;
	line-height: 1.2;
	width: 100%;
	max-width: 100%;
	overflow-x: hidden;
}

body {
	margin: 0;
	font-size: 1rem;
	color: #000000;
	font-weight: 400;
	font-family: $font1;
	text-align: left;
	width: 100%;
	max-width: 100%;
	overflow-x: hidden;
}


*,
*::before,
*::after {
	box-sizing: border-box;
}

.container {
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	max-width: 1170px;
	@include media((max-width: (lg:960px, md:720px, sm:320px)));
}

img {
	max-width: 100%;
	height: auto;
}

.btn {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 179px;
	height: 51px;
	border-radius: 63% 8% 44% 10%/86% 23% 88% 24%;
	background: #da1f8d;
	box-shadow: 0 26px 46px rgba(0, 0, 0, 0.24), 0 3px 0 #952165;
	font-family: $font6;
	color: #ffffff;
	font-size: 12px;
	font-weight: 700;
	border: none;
	cursor: pointer;
	user-select: none;
	transition-duration: .2s;

	&_color {
		&_purple {
			box-shadow: 0 29px 27px rgba(5, 93, 143, 0.22), 0 2px 0 rgba(104, 35, 154, 0.55), inset 0 -1px 0 rgba(0, 0, 0, 0.53), inset 0 1px 0 rgba(255, 255, 255, 0.75);
			background-image: linear-gradient(to top, #a650e1 0%, #b362e3 100%);
		}
	}

	&--blue {
		background-image: linear-gradient(to top, #0086d8 0%, #30bfff 100%);
		box-shadow: 0 29px 27px rgba(5, 93, 143, 0.22), 0 2px 0 rgba(0, 48, 86, 0.55), inset 0 -1px 0 rgba(0, 0, 0, 0.53), inset 0 1px 0 rgba(255, 255, 255, 0.75);
	}

	&:hover,
	&:focus {
		opacity: .7;
		outline: none;
	}
}


.block-title {
	color: #000000;
	font-size: 36px;
	font-weight: 700;
	font-family: $font2;
	font-style: italic;
	text-align: center;
	@include media((
			font-size: (md:30px,sm:20px),
	));
}

.block-subtitle {
	color: #00baff;
	font-family: $font1;
	font-size: 37px;
	font-weight: 700;
	text-align: center;
	@include media((
			font-size: (md:29px,sm:19px),
	));
}

.callback-form {
	margin: 0 auto;

	&__input-row {
		display: flex;
		justify-content: center;
		@include media((
				flex-direction: (sm: column),
				align-items: (sm: center),
		));
	}

	&__input {
		width: 191px;
		padding-bottom: 5px;
		background: transparent;
		border: none;
		color: #6d90a1;
		font-family: $font5;
		font-size: 14px;
		font-weight: 400;
		text-align: center;
		box-shadow: 0 2px 0 rgba(53, 191, 253, 0.33);
		border-bottom: 2px solid #00a8ff;
		transition: all 0.2s ease;
		margin: 0 12px;
		&+&{
			@include media((
				margin-top: (sm: 30px),
			));
		}
		&::placeholder{
			color: #6d90a1;
			font-family: $font5;
			font-size: 14px;
			font-weight: 400;
			text-align: center;
		}

		&:hover,
		&:focus {
			outline: none;
			color: #000000;
			border-color: #a5e0ff;

			&::placeholder {
				color: #000000;
			}
		}

		&:valid {
			box-shadow: 0 2px 0 rgba(138, 169, 184, 0.33);
			border-color: #6c90a1;
		}
	}

	&__button {
		margin: 30px auto 0;
		width: 198px;
	}

	&__info {
		margin: 25px auto 0;
		width: 550px;
		max-width: 100%;
		color: #000000;
		font-family: $font6;
		font-size: 14px;
		font-weight: 300;
		text-align: center;
		@include media((
				width: (md: 380px),
				font-size: (sm: 13px),
		));
	}
}


.fotoslider {
	position: relative;

	&__container {
		width: 1050px;
		max-width: 100%;
		margin: 0 auto;
	}

	&__wrapper {}

	&__slide {
		width: 749px;
		height: 412px;
		@include media((
				width: (sm: 100%),
				height: (sm: 160px),
		));
		img{
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__button {
		display: block;
		width: 33px;
		height: 33px;
		position: absolute;
		top: 215px;
		cursor: pointer;
		background: url("../img/next.svg") center center no-repeat;
		background-size: contain;
		opacity: 1;
		transition: opacity 0.3s ease;
		@include media((
				top: (sm: 85px),
		));
		&::before, &::after{
			display: none;
		}

		&:focus {
			outline: none;
		}

		&:hover {
			opacity: 0.5;
		}

		&_type {
			&_prev {
				transform: scale(-1, 1);
				left: 17px
			}

			&_next {
				right: 17px
			}
		}
	}

	&__pagination {
		width: 278px;
		height: 4px;
		margin: 29px auto 0;
		position: static;
		display: flex;
		@include media((
				width: (sm: 100%),
				margin-top: (sm: 9px),
		));
		.swiper-pagination-bullet{
			width: auto;
			height: 100%;
			border-radius: 0;
			flex-grow: 1;

			&-active {
				background: #009cff;
			}

			+.swiper-pagination-bullet {
				margin-left: 2px;
			}

			&:focus {
				outline: none;
			}
		}
	}
}

.hidden-content {
	display: none;
}


.social-fixed-block{
	display: flex;
	flex-direction: column;
	align-items: center;
	position: fixed;
	z-index: 30;
	right: 0;
	top: 50%;
	margin-top: -105px;
	box-shadow: 0 4px 8px 1px rgba(0, 103, 169, 0.18);
	border-radius: 36px 0 0 36px;
	background-color: #ffffff;
	padding: 46px 22px 46px 28px;
	@include media((
				padding: (md: 36px 22px 36px 22px),
				display: (sm: none),
	));
	&__link{
		display: block;
		width: 46px;
		height: 37px;
		&+&{
			margin-top: 35px;
		}
		img{
			display: block;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
}
